  .switch input {
    display: none; }
    .switch input:checked + .switch-state {
      background-color: #168eea; }
      .switch input:checked + .switch-state:before {
        transform: translateX(26px); }
    .switch input:focus + .switch-state {
      box-shadow: 0 0 1px #168eea; }

